/*Fonts*/

/*Elements*/

#lobby_container{
    height: 190px;
    width: 600px;
    text-align: center;
    margin: 350px auto auto auto;
}

#username_input{
    font-family: 'Roboto Mono', monospace;
    font-size: 25px;
    text-align: center;
    height: 45px;
    width: 400px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    
    
}

#username_btn{
    font-family: 'Roboto Mono', monospace;
    font-size: medium;
    color: snow;
    background-color: slategray;
    height: 51px;
    border-top-right-radius: 13px;
    border-bottom-right-radius: 13px;
    border-color: slategray;
    border-style: solid;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    opacity: 0.85;
    vertical-align: top;
}
#username_btn::-moz-focus-inner{
    border:0;
}
#username_btn:focus{
    outline: 0;
}

/*Text*/
.h1_titles{
    font-family: 'Press Start 2P', cursive;
    font-size: 50px;
    -webkit-transition: -webkit-transform 0.6s;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    -webkit-animation: lobby_breathing 2s ease-out infinite normal;
            animation: lobby_breathing 2s ease-out infinite normal;
}

.input_error{
    font-family: 'Roboto Mono', monospace;
    font-weight: bold;
    color: red;
    font-size: 13px;
}

/*Animations*/

#username_btn:hover{
    opacity: 1.0;
}

.h1_titles:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

#username_input:hover{
    box-shadow: 0px 0px 32px -12px rgba(0,0,0,0.75);
}

@-webkit-keyframes lobby_breathing{
    0%{
        -webkit-transform: scale(0.975);
                transform: scale(0.975);
    }
    40%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        -webkit-transform: scale(0.975);
                transform: scale(0.975);
    }
}

@keyframes lobby_breathing{
    0%{
        -webkit-transform: scale(0.975);
                transform: scale(0.975);
    }
    40%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        -webkit-transform: scale(0.975);
                transform: scale(0.975);
    }
}
/*Fonts*/

/*Elements*/
#rules_container{
    margin: 15px 0px 0px 15px;
}

#rules_content{
    margin: 15px 0px 0px 15px;
    background-color: rgb(214, 214, 214);
    border-radius: 10px;
}

#content_margin{
    padding: 5px 10px 5px 10px;
}

#toggle_rules{
    font-family: 'Press Start 2P', cursive;
    font-size: 20px;
    border-radius: 10px;
    border-style: solid;
    border-color: rgb(121, 121, 121);
    color: white;
    background-color: rgb(54, 54, 54);
    -webkit-transition: 0.2s;
    transition: 0.2s;
}
#toggle_rules:focus{
    outline: 0;
}
#toggle_rules::-moz-focus-inner{
    border:0;
}

/*Text*/

#rules_h3{
    font-family: 'Press Start 2P', cursive;
    font-size: 18px;
}

#rules_pre{
    font-family: 'Roboto Mono', monospace;
    font-weight: bold;
    font-size: 15px;
}

/*Animations*/

#toggle_rules:hover{
    background-color: rgb(80, 91, 102);
    border-style: solid;
}
#toggle_rules:active{
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
/*Fonts*/

/*Elements*/
#points_container{
    text-align: center;
    margin: 110px 20px 0px 0px;
}

/*Text*/
#title{
    font-family: 'Press Start 2P', cursive;
    font-size: 52px;
    -webkit-transition: -webkit-transform 0.6s;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
}

.points{
    font-family: 'Roboto Mono', monospace;
    font-size: 37px;
    -webkit-transition: -webkit-transform 0.6s;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
}

#clock{
    font-family: 'Roboto Mono', monospace;
    margin: 50px 0px 50px 0px;
    font-size: 33px;
    -webkit-transition: -webkit-transform 0.6s;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    -webkit-animation: clock_breathing 1s ease-out infinite normal;
            animation: clock_breathing 1s ease-out infinite normal;
}

#nextWin{
    font-family: 'Roboto Mono', monospace;
    font-size: 20px;
    -webkit-transition: -webkit-transform 0.6s;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
}

/*Animations*/

#title:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.points:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

#clock:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

#nextWin:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

@-webkit-keyframes clock_breathing{
    0%{
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
    }
    40%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
    }
}

@keyframes clock_breathing{
    0%{
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
    }
    40%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
    }
}
